body {
  background-color: #FCEEC8;
  overflow-x: hidden;
}

.header {
  margin-left: 18.5rem;
}

.search-container {
  width: 100%;
  height: 5rem;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: left;
  /* margin-left: 17%; */
  margin-top: -100px;
  /* background-color: #b74555; */
}

.search-container input {
  background-color: #9EDEF9;
  width: 200px;
  height: 40px;
  box-shadow: 0 5px 5px rgba(0,0,0,0.5);
  padding: .2rem 1.5rem;
  padding-left: .7rem;
  border-radius: .5rem;
  border: none;
  font-size: 18px;
  margin-left: 68.5%;
  outline: none;
}

.search-container button {
  background-color: #b74555;
  margin-left: 8px;
  width: 3rem;
  height: 45px;
  border-radius: .5rem;
  border: none;
  font-size: 20px;
  padding-top: .25rem;
  box-shadow: 0 5px 5px rgba(0,0,0,0.5);
  cursor: pointer;
  
}

.container {
  width: 90%;
  margin: auto;
  padding-top: 100px;
  display: flex;
}

.left-content, .right-content{
  flex-basis: 50%;
}

.left-content {
  margin-top: 0;
  display: grid;
  grid-template-columns: repeat(2, .5fr);
  /* grid-template-columns: repeat(auto-fix, minmax(200px ,0.5fr)); */
  margin-top: -5rem;
  gap: 2rem;
}


.card {
  width: 300px;
  /* background-color: #9EDEF9; */
  /* background-image: url("C:\Users\Jesus\Documents\ProjectsCS\react-pokemon\public\images\pixel-grass.png"); */
  border-radius: 1rem;
  box-shadow: 0 5px 5px rgba(0,0,0,0.5);
  padding: 0 1.5rem;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  transition: 0.12s ease;
  cursor: pointer;
}

.card:hover {
  transform: scale(1.02);
}

/* .card:hover img {
  transform: scale(1);
} */

/* .pokemon-number {
 
} */

.card img{
 margin-left: 2px;
 margin-right: 1px;
 transform: scale(1);
}

.right-content {
  width: 50%;
  text-align: center;
  color: black; 
  position: fixed;
  top: 130px;
  right: 10px;
  line-height: 1.8rem;
 
}

.right-content button {
  display: none;
  margin-left: -100rem;
}

.right-content img {
  margin: 2rem;
  height: 190px;
}

.right-content h1 {
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
}

.abilities {
  width: 70%;
  margin-left: 11rem; /*change with max-width*/
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  gap: 1rem;
  text-transform: capitalize;
}

.abilities .group {
  /* background-color: #b74555; */
  color: white;
  padding: .5rem;
  font-size: .75rem;
  border-radius: 8px;
}

.base-stat {
  margin-top: 1.7rem;
}

.btn-group {
  display: flex;
}

.btn-group button {
  outline: none;
  border: none;
  width: 150px;
  padding: 0.3rem 0;
  margin: 1rem;
  font-size: 17px;
  font-weight: bold;
  color: white;
  background-color: #b74555;
  border-radius: 8px;
  cursor: pointer;
}

.grid-pokemon-name {
  text-transform: capitalize;
}

.pokemon-stat-display {
  text-transform: capitalize;
}

@media (max-width: 450px) {

  body {
    width: 82%;
  }
  .header {
    margin-left: 4.5rem;
  }

  .search-container {
    margin-top: -20px;
    position: relative;
    margin-left: 25%;
  }

  .search-container input {
    margin-left: 0;
  }
  .left-content {
    margin-top: -5rem;
    margin-left: -0rem;
  }

  .left-content.show-mobile {
    display: none;
  }
  
  .card{
    width: 210px;
    font-size: 10px;
    padding: 0 1rem;
    margin-right: -30px;
    transform: scale(.9);
  }

  .card img {
    transform: scale(.8);
    margin-right: -7px;
    margin-left: -1px;
  }

  .right-content {
    top: 200px;
    right: 0;
    left: 48px;
    width: 72%;
    padding: 0 1rem;
    background-image: url(C:\Users\Jesus\Documents\ProjectsCS\react-pokemon\public\images\pixel-grass.png);
    border-radius: 12px;
    background-origin: padding-box;
    background-size: 150%;
    line-height: 1.2rem;
  
    
  }
  .right-content h1{
    margin-top: 6px;
   }

  .right-content button {
    height: 28px;
    width: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: -28px;
    margin-left: 300px;
    transition: 0.2s ease;
    border: none;
    border-radius: 8px;
  }

  .right-content button:hover {
    background-color: #b74555;
  }

  .right-content img {
    margin-left: 2rem;
    transform: scale(.8);
  }

  .right-content h3 {
    margin-bottom: 2px;
    font-size: 14px;
  }

  .right-content h1 {
    font-size: 20px;
  }

  .abilities {
    margin-top: -1px;
    width: 200%;
    margin-left: -10.5rem;
  }

  .abilities .group {
    font-size: 10px;
  }

  .btn-group button {
    width: 80px;
    font-size: 10px;
    margin-right: 0px;
  }

}

